.product-details {
  @include full-grid;

  &__title {
    color: $white;
    margin-top: 16px;
  }

  &__visible-content {
    display: flex;
    margin-top: 32px;
    position: relative;
    gap: 40px;

    @include on-tablet {
      margin-top: 40px;
      gap: 17px;
    }

    @include on-desktop {
      gap: 64px;
    }
  }

  &__id {
    color: $icons;
    position: absolute;
    top: 0;
    right: 0;
  }

  &__params {
    position: relative;

    @include on-tablet {
      max-width: 237px;
    }

    @include on-desktop {
      position: static;
      max-width: 320px;
    }
  }

  &__image {
    object-fit: contain;
    width: 288px;
    height: 288px;

    @include on-tablet {
      width: 287px;
      height: 287px;
    }

    @include on-desktop {
      width: 464px;
      height: 464px;
    }
  }

  &__images {
    display: flex;
    flex-direction: row-reverse;

    @include on-tablet{
      flex-direction: row;
    }
  }

  &__image-button {
    box-sizing: border-box;
    object-fit: contain;
    border: 1px solid $elements;
    cursor: pointer;
    width: 35px;
    height: 35px;

    @include on-desktop {
      width: 80px;
      height: 80px;
    }

    &--selected {
      border-color: $white;
    }
  }

  &__images-buttons {
    display: flex;
    gap: 8px;

    @include on-tablet {
      flex-direction: column;
    }

    @include on-desktop {
      gap: 16px;
    }
  }

  &__colors {
    display: flex;
    gap: 8px;
    margin-top: 8px;
  }

  &__param-text {
    color: $secondary;
  }

  &__color {
    box-sizing: border-box;
    width: 32px;
    height: 32px;
    border: 2px solid $black;
    border-radius: 50%;
    outline: $elements solid 1px;

    &--selected {
      outline-color: $white;
    }
  }

  &__capacities {
    margin-top: 8px;
    display: flex;
    gap: 8px;
  }

  &__capacity {
    box-sizing: border-box;
    width: 63px;
    height: 32px;
    border: 1px solid $icons;
    color: $white;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;

    &--selected {
      color: $black;
      background-color: $white;
      border-color: $white;
    }
  }

  &__capacity-block {
    padding-block: 24px;
    margin-top: 24px;
    border-block: 1px solid $icons;
  }

  &__prices {
    display: flex;
    gap: 8px;
    align-items: center;
    margin-top: 32px;
  }

  &__price {
    color: $white;
  }

  &__old-price {
    color: $secondary;
    text-decoration: line-through;
  }

  &__buttons {
    display: flex;
    gap: 8px;
    margin-top: 16px;
  }

  &__button {
    width: 263px;
    height: 48px;
  }

  &__favourite-button {
    width: 48px;
    height: 48px;
  }

  &__params-block {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-top: 32px;
  }

  &__product-params {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__param-name {
    color: $secondary;
  }

  &__param {
    color: $white;
  }

  &__back {
    box-sizing: border-box;
    width: 66px;
    height: 16px;
    display: flex;
    align-items: center;
    padding: 0 15px 0 20px;
    position: relative;
    cursor: pointer;
    margin-top: 24px;

    @include on-tablet {
      margin-top: 40px;
    }

    &-text {
      color: $white;
      position: absolute;
      left: 20px;
      top: 1px;
    }

    &::before {
      content: '';
      width: 16px;
      height: 16px;
      background-size: contain;
      background-position: center;
      transform: rotate(180deg);
      background-image: url('../../img/Icons/Arrow.png');
      position: absolute;
      left: 0;
    }
  }
}
